patchValue(morale,physique,situation:any){

  this.personneMoraleForm.patchValue({
   
    etatCivil:situation['idNmEtatCivil'],
    email:  situation['email'],
    tel:  situation['telephone'] ,
    nom:  physique['nom'],
    prenom:  physique['prenom'],
    numPiece:  physique['numeroPiece'],
    sexe:  physique['idNmSexe'],
    typePiece:physique['idTypePiece'],
    // //
    numPiecePM:  morale['numeroPiece'],
    nomPM:morale['nom'],
    statusJurid:morale['idNmStatutJurid'],
    activite:morale['activite'],
    sigle:  morale['sigle'],
    typePiecePM: morale['idTypePiece'],
   
  
   
   
  });

  this.displayNumPiece = true;
  Array.from(this.listTypePiece).filter(e=>{
    if(e['code']==="NONE" && e['id']===physique['idTypePiece']){
      this.displayNumPiece = false; 
      this.personneMoraleForm.get('numPiece').setValue(" ");
    } 
  })


}

