private markFormGroupTouched(formGroup: FormGroup) {
  (<any>Object).values(formGroup.controls).forEach(control => {
    control.markAsTouched();

    if (control.controls) {
      this.markFormGroupTouched(control);
    }
  });
}


public getIdPersonneResult(){

  return  this.idPersonneResult.asObservable();
}

openDialog(numPiecePM,listPersonnePMBynumPiece:any){

  this.conforme = false;
  const dialogConfig = new MatDialogConfig();
  dialogConfig.disableClose = true;
  dialogConfig.autoFocus = true;
  dialogConfig.direction = this.translate.getDefaultLang()=== 'ar' ? 'rtl' : 'ltr';
  dialogConfig.data = {
    dir : this.dir,
    currentLang:this.translate.getDefaultLang(),

  numPiece: numPiecePM,
  listPM:listPersonnePMBynumPiece  ,
  listEtatCivil:this.listEtatCivil,
  listTypePiece : this.listTypePiece,
  listTypePiecePM : this.listTypePiecePM,
  listSexe:this.listSexe,
  listStatusJurid : this.listStatusJurid
  };
  //  dialogConfig.width="100%";
  //  dialogConfig.height="100%";
  const dialogRef = this.dialog.open(DialogPMComponent,dialogConfig);
  dialogRef.afterClosed().subscribe(result => {



     if(result.mode ==="conforme"){

       this.patchValue(result.morale,result.physique,result.situation);
       this.disabled=true;
       this.disabledPP=true;
       this.disableSearchPP=true;
       this.disableSearchPM=true;
       this.personneMorale=result.morale;
       this.personnePhysique=result.physique;
       this.situationPersonne=result.situation;
       this.mode = result.mode;

       this.conforme = true;
    }else if(result.mode ==="modif"){
      this.patchValue(result.morale,result.physique,result.situation);
       this.disabled=false;
       this.disabledPP=false;
       this.disableSearchPP=false;
       this.disableSearchPM=false;
       this.personneMorale=result.morale;
       this.personnePhysique=result.physique;
       this.situationPersonne=result.situation;
       this.mode = result.mode;
       this.conforme = false;
     
      }else if(result.mode ==="nconforme"){
        this.mode = result.mode;
      this.resetForm();
     this.personneMoraleForm.get('numPiecePM').setValue(result.morale['numeroPiece']);
     this.mode = result.mode;
       this.disabled=false;
       this.disabledPP=false;
       this.disableSearchPP=false;
       this.disableSearchPM=false;
    }   

    if(result==="")
    this.mode = "rien";
  });

 }

 openDialogPersonnePhysique(numPiece,listPersonnePhysiueByNumPiece:any){

  const dialogConfig = new MatDialogConfig();
  dialogConfig.disableClose = true;
  dialogConfig.autoFocus = true;
  dialogConfig.direction = this.translate.getDefaultLang()=== 'ar' ? 'rtl' : 'ltr';
  dialogConfig.data = {
    dir : this.dir,
    currentLang:this.translate.getDefaultLang(),

  numPiece: numPiece,
  list:listPersonnePhysiueByNumPiece  ,
  listEtatCivil:this.listEtatCivil,
  listTypePiece : this.listTypePiece,
  listSexe:this.listSexe
  };
  // dialogConfig.width="80%";
  // dialogConfig.height="100%";
  const dialogRef = this.dialog.open(DialogComponent,dialogConfig);
  dialogRef.afterClosed().subscribe(result => {
    
    if(result.mode ==="conforme"){
      this.patchValuePersonnePhysique(result.situation,result.personne)
      this.disabledPP=true;
      this.disableSearchPP=true;
      this.mode = result.mode;
    }else if(result.mode ==="modif"){
      this.patchValuePersonnePhysique(result.situation,result.personne)
      this.disabledPP=false;
      this.disableSearchPP=false;
      this.mode = result.mode;
      this.conforme = false;
    
    }else if(result.mode ==="nconforme"){
      this.mode = result.mode;
    this.resetFormPersonnePhysique();

    this.personneMoraleForm.get('numPiece').setValue(result.personne['numeroPiece']);

      this.disabledPP=false;
      this.disableSearchPP=false;

    }

    if(result==="")
    this.mode = "rien";

    
  });

 }

