import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {TranslateService} from '@ngx-translate/core';

@Component({
    selector: 'app-add-edit-example-modal',
    templateUrl: './add-edit-example-modal.component.html',
    styleUrls: ['./add-edit-example-modal.component.scss']
})
export class AddEditExampleModalComponent implements OnInit {
    currentLang: string;
    exemples: any;

    constructor(private dialogRef: MatDialogRef<AddEditExampleModalComponent>,
                @Inject(MAT_DIALOG_DATA) data,
                private translate: TranslateService
    ) {
        this.translate.onDefaultLangChange.subscribe((language) => {
            console.log(this.translate.getDefaultLang() + ' vs ' + language.lang);
            this.currentLang = language.lang;
        });
    }

    ngOnInit() {
        this.currentLang = this.translate.getDefaultLang();

        this.exemples = [
            {value: "aa", viewValue: "ضضضضضض"}
        ];
    }

    onCancel() {
        this.dialogRef.close();
    }
}
