  validateTier(idPersonne){

  

    this.tiersService.isUniquePersonne(this.idDossier,idPersonne).subscribe((res)=>{

     const resultat = res["payload"]; 
        
     if((resultat.exist === "false" && this.mode=="ajout") || (resultat.exist === "true" && this.mode=="modif") || (resultat.exist === "false" && this.mode=="modif")){

      //save tier
      if(this.tierForm.valid){

        let tier = {
          
          "idDossier": this.idDossier,
          "idPersonne": idPersonne,
          "idNmNatureTiers": this.tierForm.get('idNmNatureTier').value,
          "id": "",
        }
  
        if(this.idDossier!=undefined)
        tier.id = this.idTier;
  
        //save tier
         this.tiersService.saveOrUpdateTiers(tier).subscribe((data)=>{
           this.getListTiersByIdDossier();
             this.edit=false;
             if(this.currentLang=='ar'){
              this.toastr.success('', ' عملية ناجحة');
             }else{
              this.toastr.success('', 'opération effectuée avec succés!');
             }
         },(err)=>{
              
         })
  
  
      }

     }else{

      if(this.currentLang=='ar'){
        this.toastr.error('', '  هذا الشخص موجود مسبقاً في قائمة الأطراف');
       }else{
        this.toastr.error('', 'Cette personne est déja existant dans la liste des tiers');
       }


     }
    },(err)=>{

      if(this.currentLang=='ar'){
        this.toastr.error('', ' خطأ داخلي يرجى الاتصال بالمسؤول');
       }else{
        this.toastr.error('', 'une erreur interne est survenue contacter votre administrateur!');
       } 

    })




  }

  delete(idElement){

 
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.direction = this.translate.getDefaultLang()=== 'ar' ? 'rtl' : 'ltr';
    dialogConfig.data = {
      currentLang:this.translate.getDefaultLang(),
    };
     dialogConfig.width="40%";

    const dialogRef = this.dialog.open(DeleteComponent,dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {

    if(result.mode ==="delete"){
           
    this.tiersService.deleteTiers(idElement).subscribe((data)=>{
      this.getListTiersByIdDossier();
      if(this.currentLang=='ar'){
        this.toastr.success('', ' عملية ناجحة');
       }else{
        this.toastr.success('', 'opération effectuée avec succés!');
       }
  })
    
      }
    });
 

  }
  

