    patchNatueTier(element:any){
      this.tierForm.get("idNmNatureTier").setValue(element.idNatureTier);
      if(element._pp){
        this.tierForm.get("flgPersonne").setValue('1');
      }else{
        this.tierForm.get("flgPersonne").setValue('0');
      }
     
      this.disableFlgP = true;
      this.disableNature = true;


    }

