import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

import { CONSTANTE_ACCOUNT_URL } from '../../constante';

@Injectable({ providedIn: 'root' })
export class AccountService {

    //public listBreadcums : Array<Object> = [] ;


    constructor(
        private router: Router,
        private http: HttpClient
    ) {
    }

    public login(username, password) {
        return this.http.post(`${CONSTANTE_ACCOUNT_URL.AUTHENTICATE}`, { username, password });
    }

    public whoiam() {
        return this.http.get(`${CONSTANTE_ACCOUNT_URL.WHOIAM}`);
    }

    public logout() {
        sessionStorage.removeItem('token');
        this.router.navigate(['/authentification/login']);
    }

    public forgotPassword() {
        sessionStorage.removeItem('token');
        this.router.navigate(['/authentification/login']);
    }

    public getFunction() {
        return this.http.get(`${CONSTANTE_ACCOUNT_URL.FUNCTION}`);
    }
}
